import React, { Component } from "react";

class Skills extends Component {
	render() {
		return (
			<div id="skills">
				<h3>Skills</h3>
				<p> Javascript  ECMAScript 2015  React.js  Handlebars.js  jQuery  AJAX  HTML5  CSS3  Sass  Node.js  MySQL  Sequelize  Mongo  Mongoose  Express.js  Webpack  Firebase  JSON  Git  Github  Gulp.js
				</p>
			</div>
		);
	}
}

export default Skills;



